.product:hover{cursor:pointer}.selected-products .product:hover{cursor:-webkit-grab;cursor:grab}.product:hover .MuiCardActionArea-focusHighlight{opacity:0.08}.selected-products .MuiCardHeader-content{overflow:hidden}.selected-products .MuiCardHeader-subheader{width:calc(100% + 48px)}.MuiCardActionArea-root{margin-top:auto !important}

.product-placeholder{background-color:#eee;border-radius:4px;border:1px dashed #9e9e9e;overflow:hidden}.product-placeholder .MuiPaper-root{opacity:0}

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow:hidden}

